import React from "react";

const DroppedCalls = (props) => {
  return (
    <>
      <div className="overview-summary">
        <h1>0</h1>
        <p className="mt-5">
          This is the total number of calls dropped by your clients while
          calling you before the 30seconds ring time is over
        </p>
      </div>
    </>
  );
};

export default DroppedCalls;
